import GlobalManager from "@/services/GlobalManager";
import FetchManager from "@/services/FetchManager";

/**
 * 
 * https://developer.work.weixin.qq.com/document/path/91770
 * @param {
 *  webhook: String webhook
 *  data: Object 
 * }
 */
export const sendRobotMsg = async (params) => {
  const { webhook, data } = params || {};
  console.log("sendRobotMsg", webhook, data);
  const res = await FetchManager.execAxiosPOST(
    `${GlobalManager.baseUrlWeiXinCloudrun}/api/post/sendRobotMsg`,
    params
  );
  return res?.data || {};
};

/** access_token
 * https://developer.work.weixin.qq.com/document/path/91039
 * @param {
 *  corpid: String ID
 *  corpsecret: String 
 * }
 */
export const getAccessToken = async (params) => {
  console.log("getAccessToken", params);
  const res = await FetchManager.execAxiosGET(
    `${GlobalManager.baseUrlWeiXinCloudrun}/api/get/getAccessToken`,
    params
  );
  return res?.data || {};
};

/** 
 * https://developer.work.weixin.qq.com/document/path/90253
 * https://open.work.weixin.qq.com/wwopen/devtool/interface?doc_id=10112
 * media_idmedia_id media_id
 * size5
 * image10MBJPG,PNG
 * voice 2MB60sAMR
 * video 10MBMP4
 * file20MB
 * @param {
 *  corpid: String ID
 *  corpsecret: String 
 *  accessToken: String 
 *  type: String  (image)(voice)(video)(file)
 *  fileMedia: File  filenamefilelengthcontent-type
 * }
 * corpid/corpsecret  accessToken , accessToken
 * multipart/form-data
 */
export const uploadQwMediaTmp = async (params) => {
  console.log("uploadQwMediaTmp", params);
  const res = await FetchManager.execAxiosPOST(
    `${GlobalManager.baseUrlWeiXinCloudrun}/api/post/uploadQwMediaTmp`,
    params,
    { headers: { "Content-Type": "multipart/form-data" } }
  );
  return res?.data || {};
};

/** 
 * https://developer.work.weixin.qq.com/document/path/91770#%E6%96%87%E4%BB%B6%E4%B8%8A%E4%BC%A0%E6%8E%A5%E5%8F%A3
 * media_idmedia_id media_id
 * @param {
 *  key: String ID
 *  type: String  (image)(voice)(video)(file)
 *  fileMedia: File  filenamefilelengthcontent-type
 * }
 * multipart/form-data
 */
export const uploadQwRobotMedia = async (params) => {
  console.log("uploadQwRobotMedia", params);
  const res = await FetchManager.execAxiosPOST(
    `${GlobalManager.baseUrlWeiXinCloudrun}/api/post/uploadQwRobotMedia`,
    params,
    { headers: { "Content-Type": "multipart/form-data" } }
  );
  return res?.data || {};
};

export default {
  sendRobotMsg,
  getAccessToken,
  uploadQwMediaTmp,
  uploadQwRobotMedia,
};
