// // 
const requirePages = require.context(
  "./views/", // 
  false, // 
  /\w+\.vue$/ // vue
);
requirePages.keys().forEach((fileName) => {
  const componentConfig = requirePages(fileName); // 
  // console.log("requirePages", componentConfig);
  app.component(
    componentConfig.default.name,
    componentConfig.default || componentConfig
  ); // componentConfig.default.namecomponentConfig.default
});
